.header-calculate-options {
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
  height: 90px;
  text-align: center;
  padding: 35px;
}

.header-calculate-options:hover:before {
    display: block;
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    top: 0px;
    left: 0px;
    border-top: 1px solid $red-1;
    border-left: 1px solid $red-1;
}
.header-calculate-options:hover:after {
    display: block;
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    top: 0px;
    right: 0px;
    border-top: 1px solid $red-1;
    border-right: 1px solid $red-1;
}

.header-calculate-options a{
  font-family: 'Ubuntu', sans-serif;
  font-size: 14px;
  color: #4E6E82;
  letter-spacing: 0;
  line-height: 20px;
}

.header-calculate-options:hover a {
  text-decoration: none;
  color: #121212;
}

.header-calculate-options:hover a:before {
    display: block;
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 0px;
    left: 0px;
    border-bottom: 1px solid $red-1;
    border-left: 1px solid $red-1;
}
.header-calculate-options:hover a:after {
    display: block;
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 0px;
    right: 0px;
    border-bottom: 1px solid $red-1;
    border-right: 1px solid $red-1;
}

.cotizador-input {
  font-family: 'Ubuntu', sans-serif;
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
  min-height: 360px;
  text-align: center;
  padding: 0 25px;
}

.cotizador-input img {
  padding: 20px 0;
}

.cotizador-input h2 {
  font-family: 'Gotham';
  font-size: 24px;
  color: #4E6E82;
  letter-spacing: 0;
  line-height: 28px;
}

.cotizador-input .form-input {
  width: 85%;
  margin: 0 auto;
}

.cotizador-input input {
	border: 0;
	border-bottom: 1px solid $red-1;
	outline: none;
	-webkit-appearance: none;
	font-size: 12px;
	text-align: center;
	color:  #4E6E82;
  width: 100%;
  text-align: left;
}

.cotizador-input span {
  color: $red-1;
  font-size: 12px;
  display: block;
  padding-top: 10px;
  text-align: left;
}

.cotizador-input .fa {
  border: 1px solid $red-1;
  padding: 1px;
}

.cotizador-input .btn{
  width: 85%;
}

.cotizador-corner-top:before {
  display: block;
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0px;
  left: 15px;
  border-top: 1px solid $red-1;
  border-left: 1px solid $red-1;
}

.cotizador-corner-top:after {
  display: block;
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0px;
  right: 15px;
  border-top: 1px solid $red-1;
  border-right: 1px solid $red-1;
}

.cotizador-results:before {
    display: block;
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 0;
    left: 15px;
    border-bottom: 1px solid $red-1;
    border-left: 1px solid $red-1;
}

.cotizador-results:after {
    display: block;
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 0;
    right: 15px;
    border-bottom: 1px solid $red-1;
    border-right: 1px solid $red-1;
}

.cotizador-results{
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
  min-height: 360px;
  text-align: center;
}

.cotizador-results input[type=radio] {
	display: none;
}

.cotizador-results label:before {
    display: block;
    content: "";
    width: 17px;
    height: 17px;
    position: absolute;
    top: -10px;
    left: 30px;
    border-top: 1px solid #DADADA;
    border-left: 1px solid #DADADA;
}

.cotizador-results label:after {
    display: block;
    content: "";
    width: 17px;
    height: 17px;
    position: absolute;
    top: -10px;
    right: 30px;
    border-top: 1px solid #DADADA;
    border-right: 1px solid #DADADA;
}

.cotizador-results input:checked + label:before {
  border-top: 1px solid $red-1;
  border-left: 1px solid $red-1;
}

.cotizador-results input:checked + label:after {
  border-top: 1px solid $red-1;
  border-right: 1px solid $red-1;
}

.cotizador-results label span:before {
    display: block;
    content: "";
    width: 17px;
    height: 17px;
    position: absolute;
    bottom: 44px;
    left: 30px;
    border-bottom: 1px solid #DADADA;
    border-left: 1px solid #DADADA;
}

.cotizador-results label span:after {
    display: block;
    content: "";
    width: 17px;
    height: 17px;
    position: absolute;
    bottom: 44px;
    right: 30px;
    border-bottom: 1px solid #DADADA;
    border-right: 1px solid #DADADA;
}

.cotizador-results input:checked + label span:before {
  border-bottom: 1px solid $red-1;
  border-left: 1px solid $red-1;
}

.cotizador-results input:checked + label span:after {
  border-bottom: 1px solid $red-1;
  border-right: 1px solid $red-1;
}

.list-options {
  padding-right: 0;
}

.option-calc .fa {
  float: left;
  transform: rotate(180deg);
}

.option-calc.active {
  background: #ffffff;
  color: #121212;
}

.option-calc.active .fa {
  color: $red-1;
  transform: rotate(0deg);
}

.option-calc {
  background: #F8F8F8;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
  display: block;
  height: 90px;
  text-align: right;
  padding: 35px 16px;
  width: 100%;
  font-size: 14px;
  color: #4E6E82;
  letter-spacing: 0;
  line-height: 20px;
  cursor: pointer;
}

.cotizador-panels {
  padding: 75px 30px;
}

.testimonial-panels {
    -ms-flex: 1;
    -webkit-box-flex: 1;
    -moz-box-flex: 1;
    -ms-box-flex: 1;
    box-flex: 1;
    min-height: 100vh;
    overflow: hidden;
    display: flex;
}

.testimonial-panel {
    background: #fff;
    color: white;
    text-align: center;
    align-items: center;
    /* Safari transitionend event.propertyName === flex */
    /* Chrome + FF transitionend event.propertyName === flex-grow */
    transition: font-size 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11), flex 0.7s cubic-bezier(0.61, -0.19, 0.7, -0.11), background 0.2s;
    font-size: 20px;
    background-size: cover;
    background-position: center;
    flex: 1;
    justify-content: space-between;
    display: flex;
    flex-direction: column;
}

.testimonial-panel1 {
    background-image: url('../img/testimony1.jpg');
}

.testimonial-panel2 {
    background-image: url('../img/testimony2.jpg');
}

.testimonial-panel3 {
    background-image: url('../img/testimony3.jpg');
}

.testimonial-panel4 {
    background-image: url('../img/testimony4.jpg');
}

/* Flex Items */
.testimonial-panel>* {
  text-align: center;
  padding: 30px 60px;
}

.testimonial-panel > div > p {
  font-size: 14px;
  color: #FFFFFF;
  letter-spacing: 0;
  line-height: 20px;
}

.testimonial-panel > div > img {
  height: 50px;
}

.proceso {
      margin-bottom: 90px;
}

.proceso h1 {
  font-family: 'Ubuntu', sans-serif;
  font-size: 24px;
  color: #000000;
  padding: 116px 0;
  text-align: center;
}

.proceso h1 span{
  display: block;
  font-size: 36px;
  color: $red-1;

}

.step-panel {
  text-align: center;
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
  padding-bottom: 22px;
}

.step-icon {
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
  border-radius: 5px;
  width: 104px;
  height: 104px;
  margin: 0 auto;
  padding: 25px;
  position: relative;
  top: -42px;
}

.step-icon:before {
    display: block;
    content: "";
    width: 22px;
    height: 22px;
    position: absolute;
    top: 0;
    left: 0;
    border-top: 1px solid $red-1;
    border-left: 1px solid $red-1;
}

.step-icon:after {
    display: block;
    content: "";
    width: 22px;
    height: 22px;
    position: absolute;
    top: 0;
    right: 0;
    border-top: 1px solid $red-1;
    border-right: 1px solid $red-1;
}

.step-icon span:before {
    display: block;
    content: "";
    width: 22px;
    height: 22px;
    position: absolute;
    bottom: 0;
    left: 0;
    border-bottom: 1px solid $red-1;
    border-left: 1px solid $red-1;
}

.step-icon span:after {
    display: block;
    content: "";
    width: 22px;
    height: 22px;
    position: absolute;
    bottom: 0;
    right: 0;
    border-bottom: 1px solid $red-1;
    border-right: 1px solid $red-1;
}
