.btn-red {
  background: $red-1;
  border-radius: 4px;
  margin: 36px 0;
  padding: 21px 9px;
  font-family: Ubuntu-Medium;
  font-size: 13px;
  color: #FFFFFF;
  letter-spacing: 0;
  line-height: 16px;
  text-align: left;
  width: 205px;
}

.btn-small {
  margin: 0;
  padding: 7px 10px;
  text-align: center;
}

.btn-red:hover {
  color: #ffffff;
}

.btn-red .fa{
  float: right;
}
