/* tablet, landscape iPad, lo-res laptops ands desktops */

@media (min-width:1115px) {
	span.nav-expander {
		display: none;
	}

.navbar {
	position: absolute;
	width: 100%;
	background-color: rgba(255, 255, 255, .6);
	box-shadow: 0 4px 2px -2px #ccc;
	border: 0;
	height: 80px;
}

#landing-section {
	background-size: 100%;
}
}


@media (min-width:801px) {
	.navbar-nav>li>a {
    line-height: 50px;
		font-family: 'Ubuntu', sans-serif;
		color: #121212 !important;
	}

	.desktop-nav-links {
		display: block;
	}

	#menu-mobile {
		display: none;
	}

	#nav-close {
		display: none;
	}


	.lead {
	   padding: 60px 0;
	}

	.step {
		background: #fff;
	  padding: 20px;
		margin: 18px 3px;

	}

	.step:hover {
		-webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);

	}

	#preguntas-frecuentes {
		height: 700px;
	}

	#preguntas-frecuentes h4 {
	  font-weight: 800;
	  letter-spacing: 2.5px;
	  color: #fff;
	  text-align: left;
	  font-size: 19px;
	}

	.question {
		padding: 20px 40px;
	}

	#preguntas-frecuentes h4::after {
	  content: '';
	  top: 76px;
	}

	.testimony-name h2::after {
		top: 111px;
	}

	.testimony-quotes {
		padding-left: 0;
	}

	#preguntas-frecuentes p {
	  font-weight: 100;
	  letter-spacing: 2px;
	  color: #fff;
	  line-height: 23px;
	  text-align: left;
	  font-size: 12px;
	  padding-top: 25px;
	}

	.cuadro-amarillo {
	  width: 300px;
	  height: 300px;
	  background: $yellow;
	  position: absolute;
	  right: 84px;
	  top: -142px;
	}

	h2.continua-titulo {
	  color: $yellow;
	  padding: 20px 0;
	  font-size: 24px;
	  text-align: left;
	}

	.continua-credito {
	  position: absolute;
	  margin: 0 13%;
	  padding:0 40px;
	  height: 248px;
	  top: -34px;
	  width: 72%;
	  background: #fff;
	  border: 1px solid #999;
	}

	.continua-cel-input {
		-webkit-appearance: none;
		-moz-appearance: none;
	  appearance: none;
		border: 0;
		border-radius: 3px;
		background: #eee;
		color: $yellow;
		text-align: center;
		padding: 5px 30px;
	  margin: 20px;
	}

	#pic-testimony {
		padding-right: 0;
		height: 100%;
		background-image: url("../img/profile.jpg");
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
	}

	.testimonios {
		height: 450px;
		padding: 0 20px;
	}

	.testimony-name {
		padding: 50px 20px;

	}

	.testimony-name span {
		font-size: 24px;
	}

	.testimony-name h2 {
		font-size: 30px;
	}

	.border-hover {
		width: 64%;
    top: 18px;
    right: 18px;
    height: 13px;
    background: #D65D98;
    position: absolute;
		opacity: 0;
	}

	.step:hover .border-hover {
		opacity: 1;
	}

	.navbar-nav {
		float: right;
	}

	.left-oval-desktop {
		opacity: 1;
		height: 50vh;
	}

	.left-oval-desktop.down {
	  opacity: 1;
	}

	.right-oval-desktop.down {
	  opacity: 1;
	}

	.right-oval-desktop {
		opacity: 1;
		height: 50vh;
	}

	.checkboxForm {
		width: 67%;
	}

	.mobile-only {
		display: none;
	}

	.panel-heading a {
		display: block;
	}

	.panel.section-panel {
		margin-top: 20px;
		box-shadow: 0 4px 9px 0;
		border-radius: 16px;
	}

	.panel.section-panel img {
		padding: 15px 0;
	}

	.desktop-nav-links {
		padding-left: 6em;
		/* padding-top: 1em; */
		margin: 10px 2px;
	}

	.desktop-nav-links input {}

	.desktop-nav-links .notifications img {
		float: right;
		height: 32px;
		padding-right: 2em;
	}

	.desktop-nav-links .search {
		padding-top: 1em;
	}

	.floating-icon {
		position: fixed;
		z-index: 2100;
	}

	span.open-menu .btn {
		color: $blue-1;
		position: absolute;
		right: 0;
		top: 50px;
		background-color: #fff;
		border: 1px solid #eee;
		padding: 10px 4px;
		box-shadow: 0 2px 3px #e8e8e8;
	}

	.btn.active.focus,
	.btn.active:focus,
	.btn.focus,
	.btn:active.focus,
	.btn:active:focus,
	.btn:focus {
		outline: none;
	}

	span.open-menu .btn i {
		font-size: 18px;
		-moz-transform: rotate(90deg);
		-webkit-transform: rotate(90deg);
		-ms--transform: rotate(90deg);
		transform: rotate(90deg);
	}

	.nav-expanded span.open-menu .btn i {
		-moz-transform: rotate(270deg);
		-webkit-transform: rotate(270deg);
		-ms--transform: rotate(270deg);
		transform: rotate(270deg);
	}

	.icon-item {
		box-shadow: 0 0 10px #e8e8e8;
		margin: 0 auto;
		text-align: center;
		width: 100px;
		height: 70px;
		background: #fff;
	}

	.sidebar-header p,
	.sidebar-header-profilepic {
		display: none;
	}

	ul.nav.navbar-nav li.notification {}

	ul.nav.navbar-nav li.profile img {
		height: 64px;
		border-radius: 50%;
	}

	ul.nav.navbar-nav li.profile-name {
		margin: 1em;
		top: 5px;
		font-weight: 600;
	}

	.sidebar-header {
		text-align: right;
	}

	.sidebar-header-profilepic img {
		height: 50px;
		border-radius: 50%;
	}

	.sidebar-header img {
		height: 30px;
		padding-right: 15px;
	}

	.sidebar-header-logo {
		padding-top: 15px;
		padding-bottom: 20px;
	}

	.main-menu li {
		margin-left: 0;
		font-size: 14px;
		padding-right: 16px;
		text-align: center;
		flex: 1;
	}

	.nav-expanded .main-menu li:hover {
		border-left: 3px solid $blue-1;
		border-right: 0;
	}

	.main-menu li:hover {
		border-right: 3px solid $blue-1;
	}

	.main-menu {
		display: flex;
		flex-direction: column;
		height: 80%;
		justify-content: space-between;
	}

	.main-menu li a span.menu-text {
		display: none;
	}

	.nav-btn-back {
		display: none;
	}

	span.icon img {
		display: inline-block;
		text-align: center;
	}

	.nav-expanded .sidebar-header {
		text-align: center;
	}

	.nav-expanded .main-menu li a span.menu-text {
		display: block;
	}

	.nav-expanded span.icon {
		text-align: center;
	}

	.nav-expanded span.icon img {
		padding: 0 !important;
	}

	.nav-expanded .main-menu li a > span.icon img {
		float: none;
	}

	.nav-expanded span.icon img {
		display: inline-block;
		text-align: center;
	}

	.nav-expanded span.icon {
		text-align: center;
	}

	.nav-expanded span.icon img {
		padding: 0 !important;
	}

	.main-menu li a > span.icon {
		width: 100%;
		display: inline-block;
		-webkit-transition: all 0.2s ease-in-out;
		-moz-transition: all 0.2s ease-in-out;
		-o-transition: all 0.2s ease-in-out;
		transition: all 0.2s ease-in-out;
	}

	.main-menu li a > span.icon img {
		float: right;
		height: 25px;
		padding-right: 15px;
	}

	.open-menu .btn {
		display: block;
	}

	body.nav-expanded .mobile-wrapper {
		display: none;
	}

	.title-section {
		font-size: 16px;
	}

	.testimonio-pic {
	    height: 100px;
	    position: relative;
	    /* margin: 0 auto; */
	    /* border-radius: 50%; */
	    padding: 0 !important;
	    top: 37px;
	    /* left: 20%; */
	}

	.scrollmenu {
		text-transform: none;
		display: flex;
	}

	.scrollmenu li {
		flex: 1;
		padding: 20px;
	}

	.scrollmenu li.active {
		color: $blue-1;
		border-bottom: 2px solid $blue-1;
	}

	.scrollmenu li span{
	    display: inline-block;
	}

	.credit-promo {
		margin: 0 auto;
	}

	.swiper-container {
		height: fit-content;
	}

	.swiper-slide {
		width: 30%;
		// min-height: 1200px;
	}

	.panel {
		background-color: transparent;
	}

	.panel-default > .panel-heading+.panel-collapse > .panel-body {
		border-top-color: transparent;
	}

	.panel-default,
	.panel-group .panel-heading+.panel-collapse > .list-group,
	.panel-group .panel-heading+.panel-collapse > .panel-body {
		border: 0;
	}

	.panel-default > .panel-heading {
		border: 1px solid $blue-1;
		text-align: center;
		background-color: transparent;
	}

	.panel-title i {
		float: right;
		color: $blue-1;
	}

	.code-input {
		font-size: 48px;
		width: 64px;
		height: 64px;
		color: #333
	}

	.chart-row {
		padding-top: 40px;
	}

	#btn-solicita-credito-fixed {
		top: 15%;
    right: 2%;
	}

	.controls .left {
		left: 25%;
	}

	.controls .right {
		right: 25% !important;
	}

	.panel-heading a:focus,
	.panel-heading a:hover {
		color: #333;
	}

	.modal-dialog {
		width: 87%;
		margin-left: 10%;
	}

	.only-mobile {
		display: none;
	}

	.only-desktop {
		display: block;
	}

	.progress-indicator li .text {
		display: block;
	}

	.steps-title-status {
		display: none;
	}

	#typed {
		font-size: 36px;
	}

	.table-responsive > .fixed-column {
		display: none;
	}

	.table.ofertas-table > tbody > tr > td {
		padding-left: 0;
	}

	.table.ofertas-table > thead > tr > th.title-ofertas {
		padding-left: 0;
	}

	.title-status {
		display: block;
	}
}
