@import 'partials/variables';
@import 'partials/fonts';
@import 'partials/corner-border';
@import 'partials/buttons';
@import 'partials/panels';

body {
	font-family: 'Gotham';
	background-color: #fff;
	color: #121212;
	position: relative;
	margin: 0;
	min-height: 100%;
}

.status {
	padding-top: 80px;
	padding-bottom: 20rem;
}

.header-section {
	padding-top: 176px;
}

.testimonials {
	font-family: 'Ubuntu', sans-serif;
	text-align: center;

	.section-header {
		width: 100%;
		background: $red-1;

		h1 {
			color: #ffffff;
			font-size: 36px;
			padding: 15px 0;
		}
	}
}

#landing-section {
	text-align: center;
	height: 100%;
	/* Center and scale the image nicely */
	background-image: url("../img/bg.jpg");
	background-repeat: no-repeat;
	background-size: cover;
}

#footer {
	background: #4E6E82;
	color: #ffffff;
}

.footer-top {
	padding: 50px 0 30px;
	text-align: center;
}

.footer-top .tel span {
	font-size: 36px;
}

.footer-top p {
	font-size: 14px;
}

.footer-top .question-contact p {
	font-size: 24px;
	color: #FFFFFF;
	letter-spacing: 0;
}

.footer-bottom {
	border-top: 1px solid #ffffff;
	padding: 15px;
}

.footer-bottom img {
	height: 50px;
}

.footer-bottom .text-footer-bottom {
	font-family: 'Gotham';
	font-size: 12px;
	color: #FFFFFF;
	letter-spacing: 0;
	line-height: 18px;
	margin-top: 28px;
}
/* importamos responsive al final */
@import 'partials/responsive';
