@font-face {
  font-family: 'Gotham';
  src: url('../fonts/gotham-medium.eot');
  src: local('☺'), url('../fonts/gotham-medium.woff') format('woff'), url('../fonts/gotham-medium.ttf') format('truetype'), url('../fonts/gotham-medium.svg#webfont') format('svg');
}

.navbar-brand {
  color: #121212 !important;
}

.navbar-brand span {
  color: $red-1;
}

.header-section h4 {
  font-family: 'Ubuntu', sans-serif;
  font-size: 36px;
  color: $red-1;
  letter-spacing: 0;
}

span.action {
  font-family: 'Ubuntu', sans-serif;
  font-size: 24px;
  color: #4E6E82;
  letter-spacing: 0;
  line-height: 30px;
  display: block;
}

#cotizador h1 {
  font-family: 'Ubuntu', sans-serif;
  overflow: hidden;
  font-size: 24px;
  color: #000000;
  letter-spacing: 0;
}

#cotizador h1:after {
  content: '';
  display: inline-block;
  width: 100%;
  margin-bottom: 4px;
  margin-right: -100%;
  margin-left: 10%;
  border-bottom: 1px solid $red-1;
}

.cotizador-big-title {
  font-size: 14px;
  color: #121212;
  letter-spacing: 0;
  line-height: 20px;
}
.cotizador-valor-md {
  font-family: 'Gotham';
  font-size: 18px;
  color: #EB0029;
}

.cotizador-results p {
  color: $red-1;
  padding: 10px 0;
}

.cotizador-results .radio-year-label {
  color: #121212 !important;
}
