li.corner-border:before {
    display: block;
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    top: 14px;
    left: -10px;
    border-top: 2px solid $red-1;
    border-left: 2px solid $red-1;
}
li.corner-border:after {
    display: block;
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    top: 14px;
    right: 0px;
    border-top: 2px solid $red-1;
    border-right: 2px solid $red-1;
}
li.corner-border a:before {
    display: block;
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 15px;
    left: -10px;
    border-bottom: 2px solid $red-1;
    border-left: 2px solid $red-1;
}
li.corner-border a:after {
    display: block;
    content: "";
    width: 20px;
    height: 20px;
    position: absolute;
    bottom: 15px;
    right: 0px;
    border-bottom: 2px solid $red-1;
    border-right: 2px solid $red-1;
}
